import PaginatedCollection from '../collections/PaginatedCollection'

import GroupCategory from '../models/GroupCategory'

export default class GroupCategoryCollection extends PaginatedCollection {
  comparator(category) {
    const prefix =
      category.get('role') === 'uncategorized' ? '2_' : category.get('protected') ? '0_' : '1_'
    return prefix + category.get('name').toLowerCase()
  }

  _defaultUrl() {
    return '/api/v1/group_categories'
  }
}
GroupCategoryCollection.prototype.model = GroupCategory

GroupCategoryCollection.optionProperty('markInactiveStudents')
